import React, { Component } from 'react';
import {Flex} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter} from 'react-router-dom'

const teamSuccess = '/static/visit/cz_team_success.png'
//Module-->
class TeamSuccess extends Component{

    //
    handleSearch = () => {
        //
        this.props.history.push(
            `${window._config.path}/result`,
            {
                type: 1,
                num: this.props.location.state.num
            },
        );
    }

    render(){

        //
        let bgStyle = {
            width: '100%',
            height: '100vh',
            background:`url(${teamSuccess}) top  no-repeat / contain`,
        };

        return (
            <div style={{background:'#ffffff'}}>
            <Helmet><title></title></Helmet>
            <div style={bgStyle}>

                <div className='success-bottom'>
                    {/* */}
                    <div className='success-number'>: {this.props.location.state.num}</div>
                    {/**/}
                    <div className='success-card'>
                        <div className='success-card-title'>:</div>
                        <div className='success-card-detail'>1-2</div>
                        <Flex>
                            <img className='success-wechat' src='/static/visit/cz_wechat.png'/>
                            <div className='success-card-hint'></div>
                        </Flex>
                    </div>
                    {/**/}
                    <div className='success-btn' onClick={this.handleSearch}></div>
                </div>

            </div>
            </div>
        );
    }
}

export default withRouter(TeamSuccess) 


