import React, { Component } from 'react';
import {List} from 'antd-mobile';
import { getSimpleDate } from '../DataForm';

//Widgets-->
class TeamItem extends Component{

    /*{
        "num": "34",
        "status": "0",
        "ordernum": "1515403714506",
        "organizer": "",
        "name": "",
        "visitTime": "2018-01-09 11:00"
    }*/
    render(){

        //
        let statusInfo = (status) => {
            console.log(status);
            switch(status){
                case "0": return ""
                case "1": return ""
                case "2": return ""
                case "4": return ""
            }
        }

        return (
            <div className="card-outer">
                <List className="card-inner">
                    <List.Item extra={this.props.datas.name}></List.Item>
                    <List.Item extra={this.props.datas.organizer}></List.Item>
                    <List.Item extra={this.props.datas.num}></List.Item>
                    <List.Item extra={this.props.datas.visitTime}></List.Item>
                    <List.Item extra={this.props.datas.ordernum}></List.Item>
                    <List.Item extra={statusInfo(this.props.datas.status)}></List.Item>
                </List>
            </div>
        );
    }
}

export default TeamItem


