import React, { Component } from 'react'
import {List, Picker, InputItem} from 'antd-mobile'
import {provList} from '../DataForm'

const inputReg = new RegExp("^[0-9a-zA-Z]+$");
//Widgets-->
class InputInfo extends Component{

    /*{
        ticketName: this.props.mTicketClass,
        areaType: null,
        cardType: [0],
        cardNum: '',
        name: ''
    }*/
    constructor(props) {
        super(props);
        this.state = this.props.inputs;
    }

    render(){
        return (
            <div className='step2-input-outer'>
            <List className="step2-input-inner">
                {/**/}
                <List.Item extra={this.state.ticketName}></List.Item>
                {/**/}
                <Picker 
                    data={this.props.cardClass}  
                    cols={1}
                    value={this.state.cardType}
                    onOk={value => {
                            this.setState({
                                cardType: value
                            }, () => {
                                this.props.setInputsData(this.state, this.props.index)
                            });
                        }}>
                    <List.Item arrow="horizontal"></List.Item>
                </Picker>
                {/**/}
                <InputItem
                    placeholder=''
                    maxLength={18}
                    value={this.state.cardNum}
                    onChange={value => {
                            if (inputReg.test(value) || '' === value){
                                this.setState({
                                    cardNum: value
                                }, () => {
                                    this.props.setInputsData(this.state, this.props.index)
                                })
                            }
                        }}></InputItem>
                {/**/}
                <Picker 
                    data={provList}  
                    cols={1}
                    value={this.state.areaType}
                    onOk={value => {
                            this.setState({
                                areaType: value
                            }, () => {
                                this.props.setInputsData(this.state, this.props.index)
                            })
                        }}>
                    <List.Item arrow="horizontal"></List.Item>
                </Picker>
                {/**/}
                <InputItem
                    placeholder=''
                    maxLength={4}
                    value={this.state.name}
                    onChange={value => {
                            this.setState({
                                name: value
                            }, () => {
                                this.props.setInputsData(this.state, this.props.index)
                            })
                        }}></InputItem>
            </List>
            </div>
        );
    }
}

export default InputInfo


