import React, { Component } from 'react'
import {Flex, Toast} from 'antd-mobile'
import {Helmet} from "react-helmet"
import {withRouter } from 'react-router-dom'
import PersonItem from '../SearchReserve/PersonItem'
import {api_select_return, api_delete_ticket} from '../Api'
import './style-return-result.css'
import { getParams } from '../DataForm';

class ReturnResult extends Component{

    //state
    constructor(props){
        super(props);
        this.state = {
            data: []
        }
    }
    
    //
    componentDidMount(){
        //console.log(getParams(api_select_return, this.props.location.state));
        fetch(api_select_return, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(this.props.location.state)
        })
            .then((response) => {
                if (response.status >= 200
                    && response.status < 300)
                    return response.json()
            })
            .then((json) => {
                if (1 == json.code)
                    return json.data
            })
            .then((data) => {
                //console.log(data)
                if (0 === data.length)
                    Toast.fail(""
                                , 1.5
                                , () => {
                                    this.props.history.go(-1);
                                });
                this.setState({
                    data: data
                });
            })
    }

    //
    handleReturn = () => {
        fetch(api_delete_ticket,  {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                ids: this.state.data[0].id
            })
        })
            .then((response) => {
                if (response.status >= 200 
                    && response.status < 300)
                    return response.json();
            })
            .then((json) => {
                //
                console.log(json);
                if (1 == json.code){
                    Toast.success ("", 1.5, () => {
                        this.props.history.go(-1);
                    });
                }
            })
    }

    render(){

        //
        let card = 0 === this.state.data.length? null : <PersonItem datas={this.state.data[0]}/> 
        let btn = 0 === this.state.data.length? null : <div className="return-result-btn" onClick={this.handleReturn}></div>

        return (
            <div style={{background: "#ffffff"}}>
                {/**/}
                <Helmet><title></title></Helmet>
                <Flex direction='column' className='card-list'>
                    {
                        card
                    }
                </Flex>
                {
                    btn
                }
                {/**/}
                <div className='cz-logo'></div>
            </div>
        );
    }
}

export default withRouter(ReturnResult) 


