import React, { Component } from 'react'
import {Flex, List, Picker, Toast} from 'antd-mobile'
import {withRouter} from 'react-router-dom'
import {ticketNum} from '../DataForm'
import DateTag from './DateTag'
import './style-step-1.css'


//Step-->Step1
class Step1 extends Component{

    //State
    constructor(props) {
        super(props);
        this.state = {
            mNum: props.mNum,
            mClass: props.mClass
        };
    } 

    //
    toNextStep = () => {
        //1. 
        if (null == this.props.mDate){
            Toast.fail("", 1.5);
            return;
        }
        if (null == this.state.mNum){
            Toast.fail("", 1.5);
            return;
        }
        if (null == this.state.mClass){
            Toast.fail("", 1.5);
            return;
        }
        //2. statestep2
        this.props.handleStep1(this.state);
    }

    //TODO: 
    handleTagSelect = (index) => {
        this.props.handleTagSelect(index);
    }

    render(){

        //TODO: 
        let tagList = this.props.mTags.map((tag, index) => {
            return <DateTag 
                        key = {index}
                        tag = {tag}
                        index = {index}
                        handleTagSelect = {this.handleTagSelect}/>
        }); 

        return (
            <div className='step1-outer-bg'>
                <div className='step1-inner-bg'>
                    {/**/}
                    <div className='step1-title' style={{marginBottom:'3.7vw'}}></div> 
                    <Flex wrap="wrap">
                        {
                            tagList
                        }
                    </Flex>
                    {/**/}
                    <div className='step1-title' style={{marginTop:'3.2vw'}}><span>5</span></div>
                    {/**/}
                    <div className='step1-title' style={{marginTop:'6.5vw', marginBottom:'2vw'}}></div>
                    <List>
                        <Picker 
                            data={ticketNum}  
                            cols={1} 
                            value={this.state.mNum}
                            onOk={value => {this.setState({mNum: value})}}>
                            <List.Item></List.Item>
                        </Picker>
                        <Picker 
                            data={this.props.mTicketClass}
                            cols={1} 
                            value={this.state.mClass}
                            onOk={value => {this.setState({mClass: value})}}>
                            <List.Item></List.Item>
                        </Picker>
                        <List.Item extra={0}></List.Item>
                        <List.Item extra={''}></List.Item>
                    </List>
                </div>

                {/**/}
                <div className='step1-bottom-btn' onClick={this.toNextStep}></div> 
            </div>
        );
    }
}

export default withRouter(Step1) 


