import React, { Component } from 'react';
import {Flex, List, InputItem, Picker, Toast} from 'antd-mobile';
import {Helmet} from "react-helmet"
import {withRouter } from 'react-router-dom'
import {reserveClass} from '../DataForm'
import '../common-search.css'

const inputReg = new RegExp("^[0-9a-zA-Z]+$");
//Module-->
class Search extends Component{

    //State
    constructor(props) {
        super(props);
        this.state = {
            type: [0],
            num: ''
        };
    } 

    //
    handleSearch = () => {
        //1. 
        if (null == this.state.type){
            Toast.fail("", 1.5);
            return;
        }
        if ('' === this.state.num){
            Toast.fail("/", 1.5);
            return;
        }
        //2. 
        this.props.history.push(
            `${window._config.path}/result`,
            {
                type: this.state.type[0],
                num: this.state.num.toUpperCase()
            },
        );
    }

    render(){
        return (
            <div className='search-content'>
            {/**/}
            <Helmet><title></title></Helmet>

            <div className="search-input-group">
                {/**/}
                <Flex wrap='wrap' className="search-input-item">
                    <div className="search-input-item-title"></div>
                    <div style={{flex:'1'}}>
                        <Picker 
                            data={reserveClass}  
                            cols={1} 
                            value={this.state.type}
                            onOk={value => {
                                this.setState({
                                    type: value
                                });
                            }}>
                            <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                    </div>
                </Flex>
                {/*/*/}
                <Flex wrap='wrap' className="search-input-item" style={{marginTop:'3.8vw'}}>
                    <div className="search-input-item-title">/</div>
                    <div style={{flex:'1'}}>
                        <InputItem 
                            placeholder=""
                            maxLength={18}
                            value={this.state.num}
                            onChange={value => {
                                if (inputReg.test(value) || '' === value){
                                    this.setState({
                                        num: value
                                    })
                                }
                            }}/>
                    </div>
                </Flex>
                {/**/}
                <div className="search-confirm-btn" onClick={this.handleSearch}></div>
            </div>
            
            {/*logo*/}
            <div className="cz-logo-relative" style={{marginTop:'52vw'}}></div>
            </div>
        );
    }
}

export default withRouter(Search)


