import React, { Component } from 'react';
import {List} from 'antd-mobile';

//Widgets-->
class PersonItem extends Component{

    /*{
        "printFlag": "0",
        "area": "",
        "ticketName": "",
        "ordernum": "1515385912878",
        "name": "",
        "date": "2018-01-09",
        "cardType": "",
        "cardNum": "222222222222222222"
    }*/
    render(){

        //
        let status = (flag) => {
            switch(flag){
                case "0": return ''
                case "1": return ''
                case "2": return ''
                case "3": return ''
            }
        }

        return (
            <div className="card-outer">
                <List className="card-inner">
                    <List.Item extra={this.props.datas.ordernum}></List.Item>
                    <List.Item extra={this.props.datas.ticketName}></List.Item>
                    <List.Item extra={this.props.datas.name}></List.Item>
                    <List.Item extra={this.props.datas.cardType}></List.Item>
                    <List.Item extra={this.props.datas.cardNum}></List.Item>
                    <List.Item extra={this.props.datas.area}></List.Item>
                    <List.Item extra={this.props.datas.date}></List.Item>
                    <List.Item extra={status(this.props.datas.printFlag)}></List.Item>
                </List>
            </div>
        );
    }
}

export default PersonItem


