import React, { Component } from 'react'
import {Flex} from 'antd-mobile'
import {Link} from 'react-router-dom'
import {Helmet} from "react-helmet"
import './style.css'

//Module-->
const path = window._config.path;
class Home extends Component{

    render(){
        return (
            <div style={{background:'#ffffff'}}>
                {/**/}
                <Helmet><title></title></Helmet>
                <div className='home-content'>
                    {/**/}
                    <div className="home-intro-area">
                        <div style={{fontSize: '4.5vw'}}></div>
                        <div style={{textIndent: '7.8vw'}}></div>
                        <div style={{textIndent: '7.8vw'}}>9:00-17:0016:00)</div>
                        <div style={{textIndent: '7.8vw'}}> 8000</div>
                        <div style={{textIndent: '7.8vw'}}></div>
                        <div style={{textIndent: '7.8vw'}}></div>
                        <div style={{textIndent: '7.8vw'}}>0519-85165089851650808078</div>
                    </div>

                    {/**/}
                    <Flex wrap="wrap" className="home-btn-group">

                        <Link to={`${path}/person`}>
                        <div className="home-btn-item">
                            <div></div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>

                        <Link to={`${path}/team`}>
                        <div className="home-btn-item">
                            <div></div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>

                        <Link to={`${path}/search`}>
                        <div className="home-btn-item">
                            <div></div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>

                        <Link to={`${path}/return-search`}>
                        <div className="home-btn-item">
                            <div></div>
                            <div className='home-btn-arrow'></div>
                        </div>
                        </Link>
                    </Flex>
                </div>
            </div>
        );
    }
}

export default Home




